export default function StyledSVG({ rotation, color }) {
  const style = {
    fill: "none",
    stroke: { color },
    strokeWidth: "4"
  };

  const svg = (
    <svg height="100" width="100" transform={rotation}>
      <polyline points="10,10,90,10,90,90" style={style} />
    </svg>
  );

  return svg;
}
